

  .navbar {
    box-sizing: content-box;
    margin-top: auto;
  
  } 

 .col {
  align-content: center;
   margin: 20px -50px 20px 40px;
 }

 .row {
  align-items: center;
 }

  h2, h3, h4, h5, h6 {
    color: #FF0095;
  }


h4 {
 
  font-size: 30px;
}

text {
color: azure;
}

.fname {
color: azure;
}


input, label {
  display: block;
}

input[type=text], select, textarea {
  width: 50%;  
  padding: 10px;  
   border: 1px solid #ccc;  
   border-radius: 10px; 
   box-sizing: border-box; 
   margin-top: 20px;  
   margin-bottom: 30px;  
   resize: vertical; 
}    


 input[type=submit] {
  background-color:#3F729B;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
} 


 input[type=submit]:hover {
  background-color: #3F729B; 
 } 


.btm-pic {
  margin-bottom: 40px;
}

